import {
  NButton,
  NForm,
  NFormItem,
  NInput,
  NInputNumber,
  NModal,
  NSelect,
  NSpace
} from 'naive-ui'

import { MapSourceType } from '@/common/map-scene-config'

import { useFormRender } from './use-form-render'
import { useModal } from './use-modal'

export default defineComponent({
  name: 'SourceModal',
  emits: ['loadMapSource'],
  setup(props, ctx) {
    const showModalRef = ref(false)
    const formRef = ref()

    const { formModel, specialLabelOptions, ...rest } = useModal()

    const { ...renderRest } = useFormRender({ formModel, specialLabelOptions })

    /**
     * 
     */
    const openModal = (key: string) => {
      formModel.value.dataType = key
      showModalRef.value = true
    }

    /**
     * 
     */
    const submitCallback = () => {
      const url = formModel.value.url
      const type = formModel.value.dataType
      const options = rest.getMapSourceOptions()
      ctx.emit('loadMapSource', { url, type, options })
      showModalRef.value = false
    }

    /**
     * 
     */
    const cancelCallback = () => {
      showModalRef.value = false
    }

    ctx.expose({
      openModal
    })

    return {
      showModal: showModalRef,
      formRef,
      formModel,
      ...rest,
      ...renderRest,
      submitCallback,
      cancelCallback
    }
  },
  render() {
    const {
      showModal,
      formModel,
      rules,
      renderImageryForm,
      renderTerrainForm,
      renderFeatureForm,
      renderSpecialForm,
      submitCallback,
      cancelCallback
    } = this
    return (
      <NModal
        v-model:show={showModal}
        closable={false}
        showIcon={false}
        preset='card'
        title=''
        bordered={false}
        style={{ width: '600px' }}
        v-slots={{
          footer: () => (
            <NSpace justify='end'>
              <NButton onClick={cancelCallback}></NButton>
              <NButton type='primary' onClick={submitCallback}>
                
              </NButton>
            </NSpace>
          )
        }}
      >
        <NForm
          ref='formRef'
          label-width='auto'
          model={formModel}
          rules={rules}
          size='medium'
        >
          <NFormItem label='' path='name'>
            <NInput v-model:value={formModel.name} placeholder='' />
          </NFormItem>
          <NFormItem label='' path='age'>
            <NSelect
              v-model:value={formModel.dataType}
              disabled
              options={this.dataTypeOptions}
            />
          </NFormItem>
          {formModel.dataType === MapSourceType.IMAGE_SERVICE
            ? renderImageryForm()
            : formModel.dataType === MapSourceType.TERRAIN_SERVICE
            ? renderTerrainForm()
            : formModel.dataType === MapSourceType.FEATURE
            ? renderFeatureForm()
            : formModel.dataType === MapSourceType.SPECIAL_SUBJECT
            ? renderSpecialForm()
            : null}
          <NFormItem label='' path='url'>
            <NInput
              v-model:value={formModel.url}
              placeholder=''
            />
          </NFormItem>
          <NFormItem label='' path='age'>
            <NSelect
              v-model:value={formModel.altitudeType}
              placeholder='Select'
              options={this.altitudeTypeOptions}
            />
          </NFormItem>
          <NFormItem label='' path='url'>
            <NSpace align='center' justify='end'>
              <span></span>
              <NInputNumber
                v-model:value={formModel.longitude}
                placeholder=''
                style={{ width: '120px' }}
              />
              <span></span>
              <NInputNumber
                v-model:value={formModel.latitude}
                placeholder=''
                style={{ width: '120px' }}
              />
              <span></span>
              <NInputNumber
                v-model:value={formModel.altitude}
                placeholder=''
                style={{ width: '120px' }}
              />
            </NSpace>
          </NFormItem>
        </NForm>
      </NModal>
    )
  }
})
